import { NavLink } from 'react-router-dom';
import React, { useEffect, useState } from 'react';
import { AboutData } from '../Components/GetData.jsx'

const Home = () => {

    const [loading, setLoading] = useState(false)
    const [data, setData] = useState([]);

    const fetchData = async () => {
        setLoading(true)

        const data = await AboutData()

        setData([...data])
        setLoading(false)
    }

    useEffect(() => {
        fetchData();
    }, []);

    return (
        <section id="Home" className="section">
            <div id="logoAbout">
                <div className="logo" id="homeLogo" style={{ backgroundImage: 'url("./Logo.png")' }}></div>
                <p id="intro">{data.length > 0 && data.map(about => (
                    about.homepage_bio
                ))}</p>
            </div>
            <div id="nav">
                <h2 className="linkNav" id='homeWork'>
                    <NavLink to="/Work"> <span>Work</span></NavLink>
                </h2>
                <h2 className="linkNav" id="homeAbout">
                    <NavLink to="/About"> <span>About</span></NavLink>
                </h2>
            </div>
        </section>
    );
};

export default Home;